import "./App.css";

import Storyfinal from "./comp/Storyfinal";

import { RouterProvider, createBrowserRouter } from "react-router-dom";
import Rootlayout from "./comp/Rootlayout";
import Hometest from "./comp/Hometest";

const router = createBrowserRouter([
  {
    path: "/",
    element: <Rootlayout />,
    children: [
      { index: true, element: <Storyfinal /> },
      // { path: "stories/1", element: <Storyfinal /> },
    ],
  },
]);

function App() {
  return <RouterProvider router={router} />;
}

export default App;
