import React, { useState } from "react";
import Stories from "react-insta-stories";



function Testtt(prop) {

    const fTest = prop.ftest;
    
    const storyHeight = '92vh'
  const [currentId, setCurrentId] = useState(0);
  
console.log(1);
    return (<>
        <div className="">
                <Stories
            currentIndex={currentId}
                    // loop={true}
                    width={432}
         height={storyHeight}
            keyboardNavigation={true}
            defaultInterval={6000}
            stories={ fTest}
        onStoryEnd={(s, st) => {
          console.log("story ended", s, st);
          setCurrentId((currentId) => currentId + 1);
        }}
        onAllStoriesEnd={(s, st) => {
          console.log("all stories ended", s, st);
          setCurrentId((currentId) => currentId = 0);
        }}
        onStoryStart={(s, st) => {
          console.log("story started", s, st);
          setCurrentId((currentId) => currentId + 1 - 1);
        }}
          />
        </div>
       
    </> );
}

export default React.memo(Testtt) ;